<template>
    <el-form :model="Register" status-icon :rules="rules" ref="Register" label-width="80px" class="form">
      <el-form-item label="" prop="username" required>
        <el-input v-model="Register.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass" required>
        <el-input type="password" v-model="Register.pass" :show-password="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPass" required>
        <el-input type="password" v-model="Register.checkPass" :show-password="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="Register.email"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('Register')"></el-button>
        <el-button @click="resetForm('Register')"></el-button>
      </el-form-item>
    </el-form>
</template>

<script>
  import api from '@/api.js'

  export default {
    data() {
      const checkSpecificKey = (str) => {
        const dist = '`~!@#$%^&*()_+{}[]:";\'\\|<>,.?/~@#%&*-_= '
        for(const key of dist) {
          if(str.includes(key)) {
            return false
          }
        }
        return true
      };
      const checkUsername = (rule, value, callback) => {
        if(!value.trim()) {
          callback(new Error(""))
        } else if(!checkSpecificKey(value)) {
          callback(new Error(''))
        }
        callback();
      };
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.Register.checkPass !== '') {
            this.$refs.Register.validateField('checkPass');
          }
          callback();
        }
      };
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.Register.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        Register: {
          username: '',
          pass: '',
          checkPass: '',
          email: '',
        },
        rules: {
          username: [
            { validator: checkUsername, trigger: 'blur'}
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          email: [
            // 
            // { required: true, message: "", trigger: 'blur' }
            { type: 'email', message: "", trigger: ['blur', 'change'] }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {

        this.$refs[formName].validate(async (valid) => {
          // console.log(valid)
          if (valid) {
            //
            const res = await api.post('register', this.gatherData())
            if(res.data.code) {
              // 
              this.$store.commit('setCurrentUser', this.Register.username)
              this.$router.push({
                  path: `/chat-room/${this.Register.username}`,
              })
            } else {
              // 
              this.$notify({
                  title: '',
                  message: '',
                  type: 'error',
                  duration: 2000,
              })
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      gatherData() {
        const data = new FormData();
        data.append('username', this.Register.username)
        data.append('password', this.Register.pass)
        data.append('email', this.Register.email)

        return data
      },
    }
  }
</script>

<style lang="stylus" scoped>

</style>