<template>
    <div>
        <headBar 
            class="bar"
            :msg="[
                 {
                    name: '',
                    idx: `/chat-room/${username}`,
                },
                {
                    name: '',
                    idx: `/chat-room/${username}/settings`,
                }
            ]" />
            
        <main class="content">
            <router-view></router-view>
        </main>
    </div>
</template>

<script>
    import headBar from '@/components/headBar'
    export default {
        props: ['username'],
        data() {
            return {

            }
        },
        components: {
            headBar,
        },
        sockets: {

        },
        mounted() {
            // 
            this.$store.commit('setCurrentUser', this.username)
            this.$socket.emit('join', this.username)
            // 
            this.$notify({
                title: '1',
                message: '',
                duration: 2000,
            })
            setTimeout(() => {
                this.$notify({
                    title: '2',
                    message: '',
                    duration: 2000,
                })
            }, 1000)
        },
        beforeDestroy () {
            // 
            this.$socket.emit('leave', this.username)
        }
    };
</script>

